.debug-ui {
  text-align: left;
  overflow-x: hidden;
  overflow-y: scroll;
  background: #fefefe;
  border-left: 1px solid #ddd;
  box-shadow: -1px 0 10px #aaa;
  position: absolute;
  width: 300px;
  right: 0;
  top: 0;
  height: 100%;
  font-family: monospace;
  font-size: 14px;
}

#debug-controls.docktop {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  min-width: 500px;
  top: 0;
  right: 300px;
  height: 50px;
  background: #fff;
  box-shadow: -3px 3px 3px #ccc;
}

@media only screen and (max-device-width: 750px) {
  .debug-ui {
    display: none;
  }
}

.debug-ui .gameinfo {
  background: #ddd;
  position: fixed;
  bottom: 0;
  box-sizing: border-box;
  width: 285px;
  margin-left: -20px;
  margin-bottom: 0;
  padding: 10px;
}

.debug-ui .gameinfo-item div {
  float: right;
  text-align: right;
}

.debug-ui .ai-visualization {
  position: fixed;
  opacity: 100%;
  right: 300px;
  height: 100%;
  width: 100%;
  max-width: 3000px;
  background: #fafafa;
  border-right: 1px solid #ddd;
}

.debug-ui .pane {
  float: left;
  padding: 20px;
  box-sizing: border-box;
  min-width: 300px;
  max-width: 400px;
  opacity: 0.8;
}

.debug-ui section {
  margin-bottom: 20px;
}

.debug-ui textarea {
  resize: none;
}

.debug-ui .move {
  cursor: pointer;
  margin-bottom: 10px;
  color: #666;
}

.debug-ui .move:hover {
  color: #333;
}

.debug-ui .move.active {
  color: #111;
  font-weight: bold;
}

.debug-ui .move-error {
  color: #a00;
  font-weight: bold;
}

.debug-ui .arg-field {
  outline: none;
  font-family: monospace;
}

.debug-ui .key {
  margin-bottom: 5px;
}

.debug-ui .key-box {
  display: inline-block;
  cursor: pointer;
  min-width: 10px;
  padding-left: 5px;
  padding-right: 5px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px #888;
  background: #eee;
  color: #444;
}

.debug-ui .key-box:hover {
  background: #ddd;
}

.debug-ui .key.active .key-box {
  background: #ddd;
  border: 1px solid #999;
  box-shadow: none;
}

.debug-ui .key-child {
  display: inline-block;
  height: 20px;
  margin-left: 10px;
}

.debug-ui .menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.debug-ui .menu .item {
  cursor: pointer;
  margin-top: -10px;
  margin-bottom: 20px;
  margin-right: 10px;
  padding: 5px;
  min-width: 50px;
  text-align: center;
}

.debug-ui .menu .item.active {
  font-weight: bold;
  border-bottom: 3px solid #ccc;
}

.debug-ui .player-box {
  display: flex;
  flex-direction: row;
}

.debug-ui .player {
  cursor: pointer;
  text-align: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  background: #eee;
  border: 3px solid #fff;
  box-sizing: content-box;
}

.debug-ui .player.current {
  background: #555;
  color: #eee;
  font-weight: bold;
}

.debug-ui .player.active {
  border: 3px solid #ff7f50;
}

